@import url(https://fonts.googleapis.com/css2?family=Gothic+A1:wght@400;700&display=swap);
.header{width:35%;height:100vh;position:fixed;display:flex;flex-direction:column}.header nav{display:flex;flex-direction:column}.header nav li{margin-bottom:1.5rem;color:#858586;font-size:12px;font-weight:bold}.header nav li a{text-decoration:none;color:#858586;letter-spacing:1.5px;text-transform:uppercase;transition:color 0.2s ease-in-out}.header nav li .line{display:inline-block;min-width:36px;min-height:1px;background-color:#858586;position:relative;top:-5px;margin:0 0.5rem;transition:background-color 0.2s ease-in-out, min-width 0.5s}.header nav li .number{font-size:11px;transition:color 0.2s ease-in-out}.header nav li:hover .line,.header nav li:active .line{background-color:#fff;min-width:54px}.header nav li:hover a,.header nav li:active a{color:white}.header nav li:hover .number,.header nav li:active .number{color:white}p{line-height:200%;font-size:14px;color:#949495}h1{font-size:50px}.mb5{margin-bottom:5rem}.skill{color:white;text-decoration:underline}i{font-size:14px}.social{margin-top:auto;margin-bottom:2rem;display:flex;justify-content:space-evenly}.social img{width:52px;height:52px;border-radius:50%;border:black 2px solid}.social span{font-size:14px;align-self:center}.social span span{margin:0 12px}

.body{width:50%;position:absolute;top:0;right:5rem;padding-top:53.5px}

.project-tab{background-color:#202022;display:flex;flex-direction:column;justify-content:space-evenly;padding:1rem 3rem 2rem;border-radius:0.5rem;transition:transform 0.2s ease-in-out;position:relative;margin:0 auto 0.5rem;max-width:600px}.project-tab h2,.project-tab h5{letter-spacing:4px}.project-tab h5{margin-bottom:0.5rem}.project-tab h2{margin-top:0;font-size:30px;margin-bottom:0.8rem}.project-tab p{margin-top:0}.project-tab:hover{transform:scale(1.1)}

.skills{display:flex;justify-content:space-evenly;align-self:center;padding-bottom:2rem}.skills i{font-size:48px}.skills img{position:relative;top:2px}

*{box-sizing:border-box}html{font-family:Gothic A1;font-weight:500;background-color:#151515;color:whitesmoke}html,body,ul{margin:0;padding:0;list-style-type:none}.container{padding:1rem 0rem 1rem 4rem}

